.Signin{
  .inner{
    min-height: 0px;
    .access{
      width: 600px; //border: 1px solid #000;
      margin: 0 auto;
      padding: 30px 20px;

      h1{
        padding: 80px 0 10px;
        text-align: center;
        font: 50px/1 'IM Fell English SC';
        letter-spacing: 2px;
        margin-bottom: 0px;
      }
      >p{
        font: 20px/1.1 'IM Fell English SC';
        margin-bottom: 40px;
      }

      .wrap{
        width: 100%;
        label[for=userid], label[for=pw]{
        display: inline-block;
        font: 18px/1 'IM Fell English SC';
        margin-bottom: 10px;
        }

        input[type=text]{
          width: 100%; height: 40px;
          background: rgba(185, 188, 186, 0.5);
          border: 2px solid #848785;
          padding: 5px 10px;
          margin-bottom: 20px;
        }

        input[type=checkbox]{
          width: 20px; height: 20px;
          margin-right: 10px;
        }
        label[for=loggedin]{
          display: inline-block;
          font: 16px/1 'IM Fell English SC';
          margin-bottom: 20px;
          position: relative; top: -7px; left: 0px;
        }

        >button{
          font: 16px/1 'IM Fell English SC';
          display: block; margin: 0 auto;
          width: 300px; height: 40px;
          background: rgba(185, 188, 186, 0.3);
          border: 1px solid #b9bcba;
          margin-bottom: 20px;
          position: relative;
          outline: none; cursor: pointer;
          box-shadow: 0 6px #9BA4A1;

          &:hover{
            box-shadow: 0 4px #9BA4A1;
            top:2px;
          }
        }
      }

      .joinyet{
        width: 100%;
        display: flex; justify-content: flex-end; align-items: center;
        margin-bottom: 60px;

        >p{
          font: 22px/1 'IM Fell English SC';
          padding: 5px 10px;
        }
        >span a{
          font: 22px/1 'IM Fell English SC';
          padding: 5px 10px;
          position: relative; top: -3px; left: 0px;
          border: none;
          background: transparent;
          color: $pink;
          cursor: pointer;
        }
      }
    }
  }
}

/* tablet */
@media screen and (max-width: $tablet){
  .Signin{
    .inner{
      width: 100%;
      .access{
        width: 70%;
        h1{
          font-size: 32px!important;
          padding: 80px 0px 10px;
        }
        >p{
          font: 20px/1.1 'IM Fell English SC';
          margin-bottom: 40px;
        }

        .wrap{
          width: 100%;
          label[for=userid], label[for=pw]{
          display: inline-block;
          font: 18px/1 'IM Fell English SC';
          margin-bottom: 10px;
          }

          input[type=text]{
            width: 100%; height: 40px;
            background: rgba(185, 188, 186, 0.5);
            border: 2px solid #848785;
            padding: 5px 10px;
            margin-bottom: 20px;
          }

          input[type=checkbox]{
            width: 20px; height: 20px;
            margin-right: 10px;
          }
          label[for=loggedin]{
            display: inline-block;
            font: 16px/1 'IM Fell English SC';
            margin-bottom: 20px;
            position: relative; top: -7px; left: 0px;
          
            span{
              font: 16px/1 'IM Fell English SC';
              color: $pink;
            }
          }

          >button{
            font: 16px/1 'IM Fell English SC';
            display: block; margin: 0 auto;
            width: 300px; height: 40px;
            background: rgba(185, 188, 186, 0.3);
            border: 1px solid #b9bcba;
            margin-bottom: 20px;
            position: relative;
            outline: none; cursor: pointer;
            box-shadow: 0 6px #9BA4A1;

            &:hover{
              box-shadow: 0 4px #9BA4A1;
              top:2px;
            }
          }
        }

        .joinyet{
          width: 100%;
          display: flex; justify-content: flex-end; align-items: center;
          margin-bottom: 60px;

          >p{
            font: 18px/1 'IM Fell English SC';
            padding: 5px 10px;
          }
          >span a{
            font: 18px/1 'IM Fell English SC';
            padding: 5px 10px;
            position: relative; top: -3px; left: 0px;
            border: none;
            background: transparent;
            color: $pink;
            cursor: pointer;
          }
        }
      }
    }
  }
}

/* mobile */
@media screen and (max-width: $mobile){
  .Signin .inner .access {width: 90%;}
  .Signin .inner .access .wrap >button {width: 100%;}
}